#Contenedor-Tabla-Subsidios-So{
    /* background-color: #242526; */
    border-radius: 20px 20px 20px 20px;
    height: 500px;
}

#Cabecera-Tabla-Subsidios-So{
    display: flex;
    height: 100px;
}

.Th-Tabla-Subsidios-So{
    /* text-transform: uppercase; */
    height: 10px;
    position: relative;
}

#Total-Cuerpo-Tabla-Subsidios-So{
    /* background-color: #2d2d2e; */
    height: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    /* padding-left: 36px; */
}

.Zona-Cuerpo-Tabla-Subsidios-So{
    width: 100%;
    /* background-color: #242526; */
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #E4E6EB;
}

.Icono-Flecha-Tabla-Subsidios-So{
    width: 35px;
    margin-right: 15px;
    margin-top: -3px;
    cursor: pointer;
    border-radius: 100px;
    padding: 10px;
}

.Icono-Flecha-Tabla-Subsidios-So:hover{
    /* background: #3A3B3C; */
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.13);
}


#Btn-Flotante-Descargar-Subsidios-So{
    background: #565656;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100px;
    width: 40px;
    height: 40px;
    position: fixed;
    bottom: 30px;
    right: 30px;
    cursor: pointer;
}

#Btn-Flotante-Descargar-Subsidios-So-Light{
    background: #FF8023;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100px;
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 30px;
    right: 30px;
    cursor: pointer;
}

#Btn-Flotante-Cargar-Subsidios-So-Light{
    background: #1EC0ED;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100px;
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 105px;
    right: 30px;
    cursor: pointer;
}

#Icono-Flotante-Descargar-Subsidios-So{
    width: 50px;
    margin-top: 5px;
    margin-left: 5px;
}

#Icono-Flotante-Cargar-Subsidios-So{
    width: 50px;
    margin-top: 5px;
    margin-left: 6px;
}


thead tr th { 
    position: sticky;
    top: 0; 
    /* background-color: #242526; */
}

.table-responsive-subsidios-so { 
    /* height:200px; */
    overflow:scroll;
    white-space: nowrap;
}

.Celda-td-Tabla-Subsidios-So{
    padding-bottom: 15px;
    /* border-bottom: 3px solid #1c1e21; */
}

#Contenedor-Filtros-Tabla-Subsidios-So{
    margin-bottom: 30px;

    display: flex;
}

.Btn-Filtro-Mostrar-Todo-Subsidios-So{
    background: #1876F2;
    border-radius: 23px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
}

.Tabla-SubsidiosSi thead th {
    position: sticky;
}
  
.Tabla-SubsidiosSi tbody th {
    position: sticky;
}

.Tabla-SubsidiosSo thead th {
    position: sticky;
}
  
.Tabla-SubsidiosSo tbody th {
    position: sticky;
}


.Columnas-Mostradas-Filtro-Columnas{
    width: 100%;
    height: 285px;
    background: #FFFFFF;
    border: 1px solid #D7E8FF;
    box-sizing: border-box;
    border-radius: 8px;
}

.Filtros-Tabla-Filtro-Columnas{
    width: 229px;
    height: 285px;
    background: #FFFFFF;
    border: 1px solid #D7E8FF;
    box-sizing: border-box;
    border-radius: 8px;
}

.Flecha-Medio-Filtro-Columnas{
    width: 17px;
    height: 17px;
    border: 1px solid #004FB8;
    box-sizing: border-box;
    border-radius: 5px;
    margin-bottom: 5px;
    cursor: pointer;
}

.Cabecera-Columnas-Mostradas-Filtro-Columnas{
    border-bottom: 1px solid #D7E8FF;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
}


.ant-modal-content{
    border-radius: 25px !important;
}

.Etiqueta-Filtro-Columnas{
    width: 100%;
    height: 21px;
    background: #FF8023;
    
    display: flex;
    align-items: center;
    padding-left: 5px;
}

.Btn-Aceptar-Modal-Excepciones{
    width: 84px;
    height: 24px;
    background: #004FB8;
    border: 1px solid #004FB8;
    box-sizing: border-box;
    border-radius: 14px;
    display: flex;
    justify-content: center;
    cursor: pointer;
    padding-top: 1px;
}

.Btn-Cancelar-Modal-Excepciones{
    width: 80px;
    height: 24px;
    background: #EDF0FA;
    border: 1px solid #004FB8;
    box-sizing: border-box;
    border-radius: 14px;
    display: flex;
    justify-content: center;
    cursor: pointer;
    padding-top: 1px;
}

.Contenedor-Btn-Aceptar-Excepciones{
    width: 84px;
    height: 24px;
}

.Input-Editar-Bultos-Subsidios-So{
    border-radius: 20px;
    padding-left: 5px;
    width: 70px;
    font-size: 10px;
}